import {
  Router,
  HashRouter,
  Switch,
  Route,
  Redirect,
  Link
} from 'react-router-dom'
import { TransitionGroup, CSSTransition } from 'react-transition-group'
import { BackTop } from 'antd'
import classNames from 'classnames'
import qs from 'qs'

import history from '@script/history'
import { userExp } from '@script/utils'
import RouteConfig from '@script/routers'

import Header from '@component/Header'
import Menu from '@component/Menu'
import SubHeader from '@component/SubHeader'

import '@assets/fonts/svg/gotop.svg'

const RouteWithSubRoutes = route => (
  <Route
    exact
    path={route.path}
    render={props => <route.component {...props} routes={route.routes} />}
  />
)
const findMatch = path => RouteConfig.find(f => f.path == path)

let userInfo, queryName, isUserCur
export default ({ store }) => {
  // console.log(store)
  return (
    <Route
      render={({ location }) => {
        const getRoutes = findMatch(location.pathname)
        const arrPathName = ['/index/article-details', '/', '/news', '/user']
        const isBackTop = arrPathName.indexOf(location.pathname) !== -1
        const isAdd = location.pathname.split('/').length < 3 && getRoutes
        const isBgWhite = location.pathname === '/share'
        const classMain = classNames('pa tl0 wh100 view-main', {
          container: isAdd,
          'container-top': !isAdd,
          'bg-white': isBgWhite
        })

        if (location.pathname === '/user') {
          userInfo = userExp()
          queryName = qs.parse(location.search.slice(1)).name
          isUserCur = userInfo && userInfo.loginname === queryName
        } else {
          isUserCur = true
        }
        return (
          <section className={classMain}>
            {getRoutes && (
              <SubHeader
                isBack={!isUserCur}
                title={getRoutes && (isUserCur ? getRoutes.name : queryName)}
              />
            )}
            <Switch>
              {RouteConfig.map((route, index) => {
                return <RouteWithSubRoutes key={index} {...route} />
              })}
              <Redirect from="" to="/" />
            </Switch>
            {isAdd && isUserCur && <Menu />}
            {isBackTop && (
              <BackTop
                className="bg-transparent ft-white"
                data-layout="layout"
                data-layout-align="center center"
              >
                <svg className="svg svg-gotop">
                  <use xlinkHref="#gotop" fill="#fff" />
                </svg>
              </BackTop>
            )}
          </section>
        )
      }}
    />
  )
}



