import axios from 'axios'
import Rstore from 'store'
import { userExp } from '@script/utils'
const isDev = process.env.NODE_ENV === 'development'
// 全局默认配置 设置 POST 请求头
axios.defaults.headers.post['Content-Type'] =
  'application/x-www-form-urlencoded'
// 配置 CORS 跨域
/*axios.defaults.withCredentials = true;
axios.defaults.crossDomain = true; */
const apiUrl = 'https://cnodejs.org/api/v1/'
let tipTime = null
const showMsg = msg => {
  tipTime && clearTimeout(tipTime)
  let formMsg = document.getElementById('form-msg')
  if (!formMsg) {
    const sDiv = document.createElement('div')
    sDiv.id = 'form-msg'
    sDiv.className = 'tc form-msg'
    sDiv.innerHTML =
      "<div data-layout-align='center center' data-layout>" + msg + '<div>'
    document.getElementsByTagName('body')[0].appendChild(sDiv)
  } else {
    formMsg.style.display = 'block'
    formMsg.innerHTML =
      "<div layout-align='center center' layout>" + msg + '<div>'
  }
  tipTime = setTimeout(function() {
    document.getElementById('form-msg').style.display = 'none'
  }, 1000)
}

export const request = options => {
  let sendData = {}
  options.method = options.method || 'get'
  return new Promise((reslove, reject) => {
    if (options.method.toLowerCase() === 'post') {
      sendData = {
        data: options.data
      }
    }
    axios({
      method: options.method,
      url: `${apiUrl}${options.url}`,
      params: options.params || {},
      ...sendData
    })
      .then(response => {
        const result = response.data
        options.success && options.success(result)
        reslove(result)
      })
      .catch(error => {
        isDev && console.error(error)
        if (error.response) {
          const [errData, errStatus] = [
            error.response.data,
            error.response.status
          ]
          options.error && options.error(errData)
          !errData.success && errData.error_msg && showMsg(errData.error_msg)
          reject(errData)
        }
      })
  })
}

Object.assign(React.Component.prototype, {
  $request: request,
  $showMsg: showMsg,
  $userExp: userExp
})



