;({
  init() {
    this.fontAdapt()
    window.onresize = () => this.fontAdapt()
  },
  fontAdapt() {
    const w = document.documentElement.offsetWidth
    const fz = w / 20
    const oEl = document.documentElement
    oEl.style.fontSize = fz < 20 ? fz : 20 + 'px'
  }
}.init())



